.timeline-container {
    position: relative;
    width: 100%;
  }
  
  .tooltip {
    position: absolute;
    padding: 10px;
    background: rgba(15, 20, 40, 0.9);
    border: 1px solid rgba(80, 200, 255, 0.5);
    border-radius: 8px;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.2s;
    font-size: 14px;
    z-index: 1000;
    color: #e0e0ff;
    box-shadow: 0 0 10px rgba(80, 200, 255, 0.3);
  }
  
  .dot {
    transition: fill 0.3s;
    fill: #50c8ff;
    filter: drop-shadow(0 0 3px rgba(80, 200, 255, 0.5));
  }
  
  .dot:hover {
    /* Using transform:scale instead of r for better CSS compatibility */
    transform: scale(1.5);
    fill: #00ffff;
    filter: drop-shadow(0 0 5px rgba(0, 255, 255, 0.8));
  }
  
  /* UFO-themed styling for timeline chart */
  .timeline-container path {
    stroke: #50c8ff;
    stroke-width: 2;
    filter: drop-shadow(0 0 3px rgba(80, 200, 255, 0.5));
  }
  
  .timeline-container text {
    fill: #c0c0ff;
    font-family: 'Exo 2', sans-serif;
  }
  
  .timeline-container .domain {
    stroke: rgba(80, 200, 255, 0.5);
  }
  
  .timeline-container .tick line {
    stroke: rgba(80, 200, 255, 0.3);
  }
  
  .timeline-container .grid line {
    stroke: rgba(80, 200, 255, 0.1);
  }
  
  /* UFO animation */
  @keyframes ufoFloat {
    0% { transform: translateY(0) rotate(0deg); }
    50% { transform: translateY(-10px) rotate(5deg); }
    100% { transform: translateY(0) rotate(0deg); }
  }
  
  .timeline-container::after {
    content: '';
    position: absolute;
    top: 20px;
    right: 20px;
    width: 40px;
    height: 20px;
    background: radial-gradient(ellipse at center, rgba(80, 200, 255, 0.8) 0%, rgba(80, 200, 255, 0) 70%);
    border-radius: 50%;
    animation: ufoFloat 4s infinite ease-in-out;
    box-shadow: 0 0 10px rgba(80, 200, 255, 0.5);
    z-index: 1;
  }
  
  .timeline-container::before {
    content: '';
    position: absolute;
    top: 10px;
    right: 25px;
    width: 30px;
    height: 10px;
    background: rgba(15, 20, 40, 0.8);
    border-radius: 50%;
    animation: ufoFloat 4s infinite ease-in-out;
    z-index: 2;
  }