.app {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    font-family: 'Exo 2', sans-serif;
  }
  
  header {
    text-align: center;
    margin-bottom: 30px;
    position: relative;
    padding: 20px 0;
  }
  
  header h1 {
    margin-bottom: 5px;
    font-size: 2.5rem;
    letter-spacing: 2px;
    position: relative;
    display: inline-block;
  }
  
  header h1::before,
  header h1::after {
    content: '';
    position: absolute;
    height: 3px;
    background: linear-gradient(90deg, rgba(80, 200, 255, 0), rgba(80, 200, 255, 0.8), rgba(80, 200, 255, 0));
    width: 150%;
    left: -25%;
  }
  
  header h1::before {
    top: -10px;
  }
  
  header h1::after {
    bottom: -10px;
  }
  
  header p {
    color: #a0a0ff;
    font-size: 1.1rem;
  }
  
  .loading, .error {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 300px;
    font-size: 20px;
    color: #a0a0ff;
    background: rgba(0, 0, 0, 0.2);
    border-radius: 10px;
  }
  
  .error {
    color: #ff6b6b;
  }
  
  .main-visualization {
    margin-bottom: 40px;
    position: relative;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0 0 20px rgba(80, 200, 255, 0.3);
  }
  
  .supporting-charts {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  @media (min-width: 768px) {
    .supporting-charts {
      grid-template-columns: 1fr 1fr;
    }
  }
  
  .chart {
    background: rgba(15, 20, 40, 0.7);
    border-radius: 15px;
    padding: 20px;
    box-shadow: 0 0 15px rgba(80, 200, 255, 0.2);
    border: 1px solid rgba(80, 200, 255, 0.1);
    position: relative;
    overflow: hidden;
  }
  
  .chart::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle, rgba(80, 200, 255, 0.05) 0%, rgba(15, 20, 40, 0) 70%);
    z-index: 0;
  }
  
  .chart-container {
    position: relative;
  }
  
  .chart-description {
    margin-top: 15px;
    padding: 15px;
    background: rgba(15, 20, 40, 0.5);
    border-radius: 10px;
    border-left: 3px solid rgba(80, 200, 255, 0.5);
  }
  
  .chart-description h3 {
    margin-top: 0;
    font-size: 1.3rem;
  }
  
  .chart-description p {
    color: #c0c0ff;
    line-height: 1.5;
    margin-bottom: 0;
  }
  
  .controls {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    gap: 20px;
    background: rgba(15, 20, 40, 0.6);
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(80, 200, 255, 0.2);
  }
  
  .control-group {
    display: flex;
    align-items: center;
  }
  
  .control-group label {
    display: flex;
    align-items: center;
    cursor: pointer;
    color: #a0a0ff;
    transition: color 0.3s;
  }
  
  .control-group label:hover {
    color: #50c8ff;
  }
  
  .control-group input[type="checkbox"] {
    margin-right: 8px;
    appearance: none;
    width: 18px;
    height: 18px;
    border: 2px solid rgba(80, 200, 255, 0.5);
    border-radius: 4px;
    background: rgba(15, 20, 40, 0.8);
    position: relative;
    cursor: pointer;
  }
  
  .control-group input[type="checkbox"]:checked {
    background: rgba(80, 200, 255, 0.8);
  }
  
  .control-group input[type="checkbox"]:checked::after {
    content: 'âœ“';
    position: absolute;
    color: #0f1428;
    font-size: 14px;
    top: -2px;
    left: 3px;
  }
  
  footer {
    margin-top: 50px;
    text-align: center;
    font-size: 14px;
    color: #a0a0ff;
    padding: 20px 0;
    position: relative;
  }
  
  footer::before {
    content: '';
    position: absolute;
    top: 0;
    left: 25%;
    width: 50%;
    height: 1px;
    background: linear-gradient(90deg, rgba(80, 200, 255, 0), rgba(80, 200, 255, 0.5), rgba(80, 200, 255, 0));
  }