.state-ranking-container {
    position: relative;
    width: 100%;
  }
  
  .tooltip {
    position: absolute;
    padding: 10px;
    background: rgba(15, 20, 40, 0.9);
    border: 1px solid rgba(80, 200, 255, 0.5);
    border-radius: 8px;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.2s;
    font-size: 14px;
    z-index: 1000;
    color: #e0e0ff;
    box-shadow: 0 0 10px rgba(80, 200, 255, 0.3);
  }
  
  .bar {
    transition: fill 0.3s;
    fill: #50c8ff;
    filter: drop-shadow(0 0 3px rgba(80, 200, 255, 0.3));
  }
  
  .bar:hover {
    fill: #00ffff;
    filter: drop-shadow(0 0 5px rgba(0, 255, 255, 0.8));
  }
  
  /* UFO-themed styling for state ranking chart */
  .state-ranking-container text {
    fill: #c0c0ff;
    font-family: 'Exo 2', sans-serif;
  }
  
  .state-ranking-container .domain {
    stroke: rgba(80, 200, 255, 0.5);
  }
  
  .state-ranking-container .tick line {
    stroke: rgba(80, 200, 255, 0.3);
  }
  
  .state-ranking-container .bar-label {
    fill: #ffffff;
    font-weight: 500;
    text-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
  }
  
  /* State highlight effect */
  @keyframes statePulse {
    0% { opacity: 0; }
    50% { opacity: 0.3; }
    100% { opacity: 0; }
  }
  
  .state-ranking-container::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at 80% 20%, rgba(80, 200, 255, 0.2), transparent 70%);
    pointer-events: none;
    z-index: 0;
  }
  
